import React from 'react'
import { Typography } from '@mui/material';

import './about.css'

const About = () => {
  return (
    <div className="dark-bg">
      <section id="about" className="container">
        <Typography variant="h2">
          ABOUT
        </Typography>
        <Typography className="about-text">
            YogaMitali is a private Yoga Centre which was formed in 2019 and which runs around the Globe. It is basically an Online Platform providing general Yoga classes in Groups, Individual, Therapy Classes to almost all age groups. The owner and the Instructor of YogaMitali is Mrs. Maitreyee Sharma, a Certified Yoga Instructor from a renowned  Institute S- Vyasa, Bengaluru. She is certified Pre and Post Natal Yoga Instructor. She is presently in her final semester of M.Sc. Yoga from Tamil Nadu Physical Education and Sports University. She is also a certified Yoga Therapist under the Indian Yoga Association and Asian Yoga Therapy Association.
        </Typography>
        <Typography className="about-text">
          Since the past 3 years, YogaMitali has been a friend to innumerable suffering Souls. Maitreyee has worked with people suffering from different variety of ailments- Alzheimer’s, Foot Drop, Infertility issues, PCOD, Gastro Intestinal problems etc. Her major area of work is with Women and Elderly population.
        </Typography>
        <Typography className="about-text">
          Countries where YogaMitali caters to are New Zealand, Australia, Canada and India. She tries her best to follow the “Classical School of Yoga”. In the present world Yoga has entered into the Glamour Industry. Maitreyee continuously fights against this mis appropriation of Yoga and shall continue working towards this aim.
        </Typography> 
      </section>
    </div>
  )
}

export default About